<template>
  <div class="lySection_wrapper">
    <div class="wrapper_content">
      <ul class="content_items">
        <template
          v-for="item in list"
          :key="item.id"
        >
          <li class="item">
            <div class="item_head">
              <img
                :src="item.imgUrl"
                alt
              />
            </div>
            <div class="item_content">
              <div class="item_content_name">
                <span class="name">{{item.name}}</span>
                <span class="time">{{item.time}}</span>
              </div>
              <div class="item_content_text">{{item.content}}</div>
            </div>
            <div class="item_reply">回复</div>
          </li>
        </template>
      </ul>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, watch } from 'vue'
import { useStore } from 'vuex'
// 从store拿数据渲染评论
const useGetlyEffect = () => {
  const store = useStore()
  store.dispatch('getlyContent')
  const getList = reactive({ list: [] })
  watch(() => store.state.lycontent, (val, old) => {
    getList.list = val
  })
  const { list } = toRefs(getList)
  return {
    list
  }
}
export default {
  name: 'lySection',
  setup (props) {
    const { list } = useGetlyEffect()
    return {
      list
    }
  }
}
</script>

<style lang="scss">
.lySection_wrapper {
  width: 100%;
  .wrapper_content {
    width: 90%;
    margin: 0 auto;
    .content_items {
      width: 100%;
      margin: 0;
      padding: 0;
      .item {
        position: relative;
        width: 100%;
        list-style: none;
        border: 0.01rem solid #ededed;
        padding: 0.1rem 0.15rem;
        margin-bottom: 0.3rem;
        &::after {
          display: block;
          content: '';
          clear: both;
        }
        &_head {
          float: left;
          width: 0.5rem;
          height: 0.5rem;
          border-radius: 50%;
          // background-color: chocolate;
          overflow: hidden;
          img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
        &_content {
          float: left;
          display: flex;
          flex-direction: column;
          // height: 0.5rem;
          padding-left: 0.1rem;
          &_name {
            margin-bottom: 0.1rem;
            .name {
              display: block;
              font-size: 0.18rem;
              font-weight: 600;
              margin-bottom: 0.04rem;
            }
            .time {
              display: block;
              font-size: 0.1rem;
              color: #aaaa;
            }
          }
          &_text {
            font-size: 0.14rem;
          }
        }
        &_reply {
          display: none;
          position: absolute;
          top: 0;
          right: 0.1rem;
          width: 0.5rem;
          height: 0.2rem;
          font-size: 0.12rem;
          background-color: #000;
          color: #fff;
          text-align: center;
          line-height: 0.2rem;
          cursor: pointer;
        }
        &:hover {
          .item_reply {
            display: block;
          }
        }
      }
    }
  }
}
</style>
