<template>
  <div class="classify">
    <div class="classify_title">
      <span>文章分类</span>
    </div>
    <ul class="classify_content">
      <div
        v-for="(item,index) in list"
        :key="item.id"
      >
        <li
          class="ify_item"
          @click="handleClickFiy(index)"
        >
          <span>{{item.calssfiy}}({{item.number}})</span>
        </li>
      </div>
    </ul>
  </div>
</template>

<script>
import { get } from '../../utils/request'
import { reactive, toRefs } from 'vue'
import { useStore } from 'vuex'
// 获取文章分类
const useFiyEffect = () => {
  const classfiy = reactive({ list: [] })
  const getclassfiy = async () => {
    const result = await get('/api/boke', {})
    classfiy.list = result
  }
  getclassfiy()
  const { list } = toRefs(classfiy)
  return {
    list
  }
}
// 修改分类下标
const useFiyIndex = () => {
  const store = useStore()
  const handleClickFiy = index => {
    // console.log(index)
    store.commit('changeFiyIndex', index)
  }
  return {
    handleClickFiy
  }
}
export default {
  name: 'BokeClassfiy',
  setup(props, context) {
    const { list } = useFiyEffect()
    const { handleClickFiy } = useFiyIndex()
    return {
      list,
      handleClickFiy
    }
  }
}
</script>

<style lang="scss" scoped>
.classify {
  display: block;
  width: 3.72rem;
  height: 2.27rem;
  border: 0.01rem solid #e3eaef;
  background-color: #fff;
  border-radius: 0.05rem;
  &_title {
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
    border-bottom: 0.01rem solid #e3eaef;
    padding-left: 0.1rem;
    box-sizing: border-box;
    font-weight: 700;
    color: #444;
    span {
      display: block;
      font-size: 0.18rem;
    }
  }
  &_content {
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 0.1rem 0;
    &::after {
      display: block;
      content: '';
      clear: both;
    }
    .ify_item {
      float: left;
      list-style: none;
      width: 1.7rem;
      height: 0.35rem;
      line-height: 0.35rem;
      background-color: #f2f2f2;
      margin: 0.1rem 0.08rem;
      border-radius: 0.1rem;
      padding-left: 0.15rem;
      box-sizing: border-box;
      cursor: pointer;
      &:hover {
        background-color: #6b7184;
        span {
          color: #fff;
        }
      }
      span {
        display: block;
        font-size: 0.14rem;
        letter-spacing: 0.03rem;
        color: #6b7184;
      }
    }
  }
}
</style>
