<template>
  <div class="article">
    <ul class="article_content">
      <div
        v-for="item in listItems.length == 0 ? allList : listItems"
        :key="item.id"
        @click="handle"
        @mousedown="handlefne"
      >
        <li class="article_content_item">
          <div class="item_title">
            <span>{{ item.artcile }}</span>
          </div>
          <div class="item_time">
            <span>{{ item.time }}</span>
          </div>
          <div class="item_img">
            <img
              :src="item.imgurl"
              alt
            />
          </div>
        </li>
      </div>
    </ul>
  </div>
</template>

<script>
import { reactive, toRefs, watch } from 'vue'
import { useStore } from 'vuex'
import { get } from '../../utils/request'
// 获取文章列表
const useBokeArticle = () => {
  const store = useStore()
  // 文章总表，分表
  const article = reactive({ allList: [], listItems: [] })
  // 监听文章分类
  watch(
    () => store.state.articleIndex,
    (now, old) => {
      let index = store.state.articleIndex
      index = now
      getarticle(index)
    }
  )
  const getarticle = async index => {
    article.allList = []
    article.listItems = []
    const reslut = await get('/api/boke/allarticale', {})
    article.allList = reslut
    for (let i = 0; i < reslut.length; i++) {
      if (reslut[i].fiy_id === index) {
        article.listItems.unshift(reslut[i])
      }
    }
  }
  getarticle()
  const { allList, listItems } = toRefs(article)
  return {
    allList,
    listItems
  }
}
export default {
  name: 'BokeArticle',
  setup(propr, context) {
    const { allList, listItems } = useBokeArticle()
    return {
      allList,
      listItems
    }
  }
}
</script>

<style lang="scss" scoped>
.article {
  flex: 1;

  &_content {
    width: 90%;
    margin: auto;
    padding: 0;

    &_item {
      width: 100%;
      height: 3rem;
      list-style: none;
      margin-bottom: 0.5rem;
      background-color: #fff;
      border-radius: 0.05rem;
      border: 0.01rem solid #e3eaef;
      padding: 0.15rem 0.13rem;
      box-sizing: border-box;
      cursor: pointer;

      .item_title {
        width: 100%;
        height: 0.5rem;
        line-height: 0.5rem;

        span {
          display: block;
          font-size: 0.3rem;
          font-weight: 600;
          color: #636b6f;
        }
      }

      .item_time {
        width: 100%;
        height: 0.15rem;
        line-height: 0.15rem;
        margin-bottom: 0.13rem;

        span {
          display: block;
          font-size: 0.15rem;
          color: #999;
        }
      }

      .item_img {
        width: 100%;
        height: 1.8rem;
        background-color: pink;

        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
}
</style>
