<template>
  <div class="lyInput_wrapper">
    <div class="wrapper_content">
      <textarea
        name
        id
        placeholder="请输入内容"
        v-model="lyText"
      ></textarea>
      <div class="btn">
        <button @click="handlesetLyText">提交</button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'
import { useStore } from 'vuex'
// 获取评论
const useLyContentEffect = () => {
  const lyText = ref('')
  const store = useStore()
  const handlesetLyText = () => {
    if (lyText.value === '') {
      alert('hhhh')
      return true
    }
    const data = {
      id: 9,
      name: 'tyu',
      time: '2020/6/47',
      content: lyText.value,
      imgUrl: 'https://gravatar.loli.net/avatar/99a751e435d14caa690a50a27e2083a2?size=80&d=https%3a%2f%2fgravatar.loli.net%2favatar%2f9e63c80900d106cbbec5a9f4ea433a3e.jpg%3fsize%3d80'
    }
    store.commit('ChangeSetLyTexr', data)
    lyText.value = ''
  }
  return {
    lyText, handlesetLyText
  }
}
export default {
  name: 'lyInput',
  setup (props) {
    const { lyText, handlesetLyText } = useLyContentEffect()
    return {
      lyText, handlesetLyText
    }
  }
}
</script>

<style lang="scss" scoped>
.lyInput_wrapper {
  width: 100%;
  padding-bottom: 0.1rem;
  .wrapper_content {
    width: 90%;
    margin: 0 auto;
    border: 0.01rem solid #ededed;
    textarea {
      display: block;
      width: 100%;
      height: 1rem;
      margin: 0 auto;
      border: none;
      outline: none;
      font-size: 0.14rem;
      resize: none;
      padding: 0.1rem 0.15rem;
      box-sizing: border-box;
    }
    .btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 0.6rem;
      margin: 0 auto;
      background-color: #fafafa;
      button {
        width: 0.6rem;
        height: 0.3rem;
        font-size: 0.14rem;
        border-radius: 0.1rem;
        border: 0.01rem solid #aaa;
        cursor: pointer;
      }
    }
  }
}
</style>
